function confirm(title, content, okCall) {
  swal({
    title,
    text: content,
    icon: 'warning',
    buttons: ['', ''],
    dangerMode: true
  })
    .then((willDelete) => {
      if (willDelete) {
        okCall()
      }
    })
}

function confirminput(title, code, content, okCall) {
  projectApi.companygetidentity({ code: code }).then(resp => {
    if (resp.status === 200) {
      swal({
        title: title,
        text: '',
        content: 'input',
        showCancelButton: true,
        closeOnConfirm: false,
        animation: 'slide-from-top',
        inputPlaceholder: '...',
        buttons: ['', '']
      }).then((willDelete) => {
        if (willDelete === false) {
          return false
        }
        if (willDelete === '') {
          error('')
          return false
        }
        if (willDelete) {
          okCall(willDelete)
        }
      })
    }
  }, error => {
    // error('', findErrorMsg(error))
    notify.error({
      title: '',
      message: findErrorMsg(error)
    })
  })
}

