function matchMsgCode(code, default_) {
  switch (code) {
    case 401:
      return ''
    case 403:
      return ''
    case 404:
      return ''
    case 429:
      return ''
    case 500:
      return ' '
    case 1001:
      return ''
    case 1002:
      return ''
    case 1004:
      return ''
    case 1005:
      return ''
    case 1010:
      return 'Mac'
    case 1011:
      return ''
    case 1012:
      return ''
    case 1013:
      return ''
    case 1014:
      return ''
    case 1015:
      return ''
    case 1016:
      return ''
    case 1021:
      return ''
    case 1022:
      return ''
    case 1023:
      return ''
    case 1031:
      return ''
    case 1032:
      return ''
    case 1040:
      return ''
    case 1050:
      return ''
    case 1051:
      return ''
    case 1061:
      return ''
    case 1062:
      return ''
    case 1063:
      return ''
    case 1070:
      return ''
    case 2001:
      return ''
    case 2011:
      return ''
    case 2012:
      return ''
    case 2013:
      return ''
    case 2014:
      return ''
    default:
      return default_
  }
}
function findErrorMsg(error_) {
  console.dir(error_)
  let msg = ''
  if (error_.response) {
    if (error_.response.data) {
      msg = matchMsgCode(error_.response.data.status || error_.response.status, error_.response.data.message)
    } else {
      msg = ''
    }
  } else {
    msg = ''
  }
  return msg
}
